.footer {
  background-color: #fafafa;
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;

  &__wrapper {
    @media (max-width: $desktop) {
      width: 100%;
    }

    @media (max-width: $tablet) {
      width: 100%;
    }
  }

  &__nav_footer {
    padding: 45px 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media (max-width: $desktop) {
      padding: 0;
      flex-direction: column;
    }

    &__navigation {
      margin-left: auto;
    }
  }

  &__copyright {
    display: block;

    @media (max-width: $desktop) {
      display: none;
    }
  }

  &__dev {
    width: 37%;
    color: rgba(27, 45, 55, 0.5);
    font-family: "PT Mono", sans-serif;
    text-align: right;

    @media (max-width: $tablet) {
      margin: 20px 0;
      width: 100%;
      text-align: center;
    }
  }

  &__confidentiality {
    color: rgba(27, 45, 55, 0.5);
    font-family: "PT Mono", sans-serif;

    @media (max-width: $tablet) {
      margin: 20px 0;
    }
  }

  &__info {
    box-sizing: border-box;
    color: rgba(27, 45, 55, 0.5);
    font-family: "PT Mono", sans-serif;
    padding: 20px 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 13px;
    line-height: 24px;
    letter-spacing: 0.04em;
    text-transform: uppercase;

    @media (max-width: $desktop) {
      border-top: 1px solid #acaaaa;
      padding: 20px 26px;
    }

    @media (max-width: $tablet) {
      flex-direction: column;
      border-top: 1px solid $darkBorder;
    }
  }

  &__date {
    display: block;

    @media (max-width: $desktop) {
      display: none;
    }

    @media (max-width: $tablet) {
      display: none;
    }
  }
}

.nav_footer {
  &__logo {
    display: flex;
    align-items: center;
    text-decoration: none;

    @media (max-width: $tablet) {
      margin-bottom: 26px;

      .logo__name {
        display: block;
      }
    }
  }

  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;

    p {
      display: none;
      font-size: 13px;
      line-height: 24px;
      letter-spacing: 0.04em;
      text-transform: uppercase;
      color: rgba(27, 45, 55, 0.5);
    }

    @media (max-width: $desktop) {
      flex-direction: row;
      width: 100%;
      justify-content: space-between;
      padding: 20px 26px;
      border-bottom: 1px solid rgba(27, 45, 55, 0.5);

      p {
        display: block;
      }
    }

    @media (max-width: $tablet) {
      flex-direction: column;
      margin-bottom: 0;
      margin-top: 24px;
      border: none;

      p {
        display: block;
      }
    }

    ul {
      display: flex;
    }
  }

  &__social {
    display: flex;
    margin-top: 65px;

    @media (max-width: $desktop) {
      margin: 0;
    }

    li {
      margin-right: 15px;
    }

    svg {
      fill: #cdcdcd;
      transition: fill 0.3s linear;

      &:hover {
        fill: #413f3f;
        transition: fill 0.3s linear;
      }
    }
  }

  &__navigation {
    margin-top: 18px;
    display: flex;
    justify-content: flex-end;
    width: 70%;
    box-sizing: border-box;

    @media (max-width: $tablet) {
      margin-top: 11px;
    }

    h3 {
      color: #1b2d37;
      margin: 0;
      padding-bottom: 28px;
      text-transform: uppercase;
      font-size: 15px;
      line-height: 16px;

      @media (max-width: $tablet) {
        padding: 28px 20px;
        width: 100%;
        border-top: 1px solid $darkBorder;
        border-bottom: 1px solid $darkBorder;
      }
    }

    @media (max-width: $desktop) {
      width: 100%;
      padding: 20px 26px;
      padding-bottom: 35px;
    }

    @media (max-width: $tablet) {
      flex-direction: column;
      padding: 20px 0;
      padding-bottom: 35px;
    }

    ul {
      height: 93px;
      width: auto;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      flex-wrap: wrap;

      @media (max-width: $desktop) {
        height: auto;
      }

      @media (max-width: $tablet) {
        width: 100%;
        flex-wrap: initial;
        height: auto;
      }

      li {
        line-height: 23px;
        font-size: 15px;

        @media (max-width: $desktop) {
          line-height: 25px;
        }
      }
    }
  }

  &__navigation_list {
    display: flex;
    min-width: max-content;
    width: 100%;
    align-items: flex-start;
    flex-direction: column;

    ul {
      width: 523px;

      @media (max-width: $desktop) {
        width: 100%;
      }
    }

    @media (max-width: $desktop) {
      align-items: flex-start;
    }

    @media (max-width: $tablet) {
      align-items: center;
    }

    h3 {
      box-sizing: border-box;
      position: relative;

      @media (max-width: $tablet) {
        align-self: flex-start;

        &::before {
          display: block;
          content: "";
          background: none;
          background-repeat: no-repeat;
          background-position: 50% 50%;
          width: 16px;
          transform: translate(0, -50%);

          height: 16px;
          position: absolute;
          right: 20px;
          top: 50%;
        }

        &.hidden::before {
          background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M8.5 7.5V0H7.5V7.5H0V8.5H7.5V16H8.5V8.5H16V7.5H8.5Z' fill='%23C4C4C4'/%3E%3C/svg%3E");
          background-repeat: no-repeat;
          background-position: 50% 50%;
        }

        &.visible::before {
          background-image: url("data:image/svg+xml,%3Csvg width='16' height='2' viewBox='0 0 16 2' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 0.5H16V1.5H0V0.5Z' fill='%23C4C4C4'/%3E%3C/svg%3E%0A");
          background-repeat: no-repeat;
          background-position: 50% 50%;
        }
      }
    }

    @media (max-width: $tablet) {
      width: 100%;

      ul {
        display: flex;
        align-items: flex-start;
        width: 93%;

        &.hidden {
          display: none;
        }
      }
    }
  }

  &__contacts {
    width: 47%;
    display: flex;
    align-items: flex-start;
    flex-direction: column;

    @media (max-width: $desktop) {
      align-items: flex-start;
    }

    @media (max-width: $tablet) {
      align-items: center;
    }

    h3 {
      box-sizing: border-box;
      position: relative;

      @media (max-width: $tablet) {
        align-self: flex-start;


        &::before {
          display: block;
          content: "";
          background: none;
          background-repeat: no-repeat;
          background-position: 50% 50%;
          width: 16px;
          transform: translate(0, -50%);

          height: 16px;
          position: absolute;
          right: 20px;
          top: 50%;
        }

        &.hidden::before {
          background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M8.5 7.5V0H7.5V7.5H0V8.5H7.5V16H8.5V8.5H16V7.5H8.5Z' fill='%23C4C4C4'/%3E%3C/svg%3E");
          background-repeat: no-repeat;
          background-position: 50% 50%;
        }

        &.visible::before {
          background-image: url("data:image/svg+xml,%3Csvg width='16' height='2' viewBox='0 0 16 2' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 0.5H16V1.5H0V0.5Z' fill='%23C4C4C4'/%3E%3C/svg%3E%0A");
          background-repeat: no-repeat;
          background-position: 50% 50%;
        }
      }
    }

    @media (max-width: $tablet) {
      width: 100%;

      ul {
        display: flex;
        align-items: flex-start;
        width: 93%;

        &.hidden {
          display: none;
        }
      }
    }

    li {
      line-height: 20px;
      font-size: 15px;
      color: rgba(27, 45, 55, 0.5);
      font-family: "PT Mono", sans-serif;
      white-space: nowrap;

      @media (max-width: $tablet) {
        white-space: pre-wrap;
      }
    }
  }

  &__link {
    color: rgba(27, 45, 55, 0.5);
    font-family: "PT Mono", sans-serif;
  }
}
